import {GET_BOT, GET_DATE_BOT, GET_TIME_BOT} from "../actionTypes";

export const getBot = () => {
	return {
		type: GET_BOT
	}
};

export const getDateBot = (value) => {
	return {
		type: GET_DATE_BOT,
		payload: value
	}
};

export const getTimeBot = (value) => {
	return {
		type: GET_TIME_BOT,
		payload: value
	}
};