import React, {Component} from 'react';
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import './input-bot.scss';

import { getBot } from '../../actions';

class InputBot extends Component {

	
	updateBot = () => {
		this.props.getBot();
	};

	renderBotBlock = (bool) => {
		if(bool) {
			return (
				<div>
					<input type="date" name="datebot" className="input-bot__data"/>
					<input type="time" name="timebot" className="input-bot__data"/>
				</div>
			);
		} else {
			return null
		}
	};


	render() {
		const { bot } = this.props;

		return (
			<div className="input-bot">
				<label>
					<input type="checkbox" name="bot" onChange={this.updateBot}/>
					Reminder-Bot
				</label>
				{
					this.renderBotBlock(bot)
				}
			</div>

		);
	}
}

const mapStateToProps = state => {
	return {
		bot: state.bot
	}
};

const mutchDispatchToProps = dispatch => {
	return bindActionCreators({
		getBot
	}, dispatch)
};

export default connect(mapStateToProps, mutchDispatchToProps)(InputBot)