import {GET_BOT, GET_DATE_BOT, GET_TIME_BOT} from "../actionTypes";

const initialState = false;

export const bot = (state = initialState, {type}) => {
	switch (type) {
		case GET_BOT:
			const oldState = state;
			return !oldState;
		default:
			return state;
	}
};

// Дата
const initialDateState = {
	month: '',
	day: ''
};

export const dateBot = (state = initialDateState, {type, payload}) => {
	switch (type) {
		case GET_DATE_BOT:
			const arr = payload.split('-');
			return {...state, month: getMonthName(arr), day: arr[2]};
		default:
			return state;
	}
};

const getMonthName = (arr) => {
	const arrMonth = ["january", "february", "march", "april", "may", "june", "july", "august", "september", "october", "november", "december"];

	const numMonth = parseInt(arr[1]);
	return arrMonth[numMonth - 1];
};

//Время
const initialTimeState = '';

export const timeBot = (state = initialTimeState, {type, payload}) => {
	switch (type) {
		case GET_TIME_BOT:
			return payload;
		default:
			return state;
	}
};

