import React, {Component, Fragment} from 'react';
import {connect} from "react-redux";

class DiscordInfo extends Component {

	renderDiscordInfoBlock = () => {
		const { startCity, meetTime } = this.props.baseInfo;

		const { month, day } = this.props.dateBot;

		if(this.props.bot) {
			return (
				<div className="info__block">
					<pre>$natural on the {day}th of {month} at {this.props.timeBot} send @everyone</pre>
					<pre>Старт: {startCity}</pre>
					<pre>{meetTime}</pre>
					<pre>Подробнее:</pre>
					<pre>https://vk.com/vtcda</pre>
					<pre>{this.props.link} to #анонс</pre>
				</div>
			)
		} else {
			return (
				<div className="info__block">
					<pre>@everyone</pre>
					<pre>Старт: {startCity}</pre>
					<pre>{meetTime}</pre>
					<pre>Подробнее:</pre>
					<pre>https://vk.com/vtcda</pre>
					<pre>{this.props.link}</pre>
				</div>
			);
		}
	};

	render() {
		return (
			<Fragment>
				{ this.renderDiscordInfoBlock() }
			</Fragment>
		);
	}
}

const mapStateToProps = state => {
	return {
		baseInfo: state.baseInfo,
		link: state.link,
		bot: state.bot,
		dateBot: state.dateBot,
		timeBot: state.timeBot
	}
};

export default connect(mapStateToProps)(DiscordInfo);